src/Flower_User_Delete_Dialog<template>
    <Top_Menu />
    <Flower_Question_Dialog v-if="open_dialog" @questionDialog="questionCloseDialog" />
    <div class="question-main">
        <div class="question-text" v-if="!img_select">
            <p>    !</p>
        </div>
        <div class="question-text2" v-if="img_select">
            <p class="question-test2-title">   !</p>
            <div class="question-title">
                <div class="essential-title" v-if="question_title_blank">*</div>
                <input class="question-input-title" type="text" placeholder=" ."
                    v-model="flower_question_title">
            </div>

        </div>

        <div class="question-upload" @click="imageUpload">
            <span class="nuki-range"></span>
            <p v-if="!img_select"> </p>
            <img v-show="img" class="question-img-circle" src="../assets/images/img-circle.png" alt="no">
            <img v-show="img" class="question-img" :src=img id="imgObj" draggable="true">
            <input v-if="!img" ref="file" accept="image/*" type="file" style="display: none" @change="changeFile" />
        </div>

        <div class="btn-box">
            <input class="reset-img-button" type="button" value="" @click="imgReset">
            <input class="sending-img-button" type="button" value="  " @click="drawImage" />
        </div>
    </div>
</template>

<script src="./Flower_Question.ts"></script>
<style src="./Flower_Question.css"></style>