import { Options, Vue } from 'vue-class-component';
import { useStore } from 'vuex';
import axios from 'axios';

@Options({
  components: {

  },
})


export default class Flower_User_Delete_Dialog extends Vue {
  public store = useStore();

  public mounted() {
  }

  //    
  public userCancel() {
    this.$emit('exit');
  }

  //   
  public userClick() {
    axios.post('http://localhost:3000/userDelete', {id: this.store.state.loginId})
    .then()
    .catch(error => {console.error(':', error);});
    this.$emit('deleteTrue');

  }
}
