import { createStore } from 'vuex';
import createPersistedState from 'vuex-persistedstate';
import axios from "axios"

export default createStore({
  state: {
    show_links: true,
    loginStatus: false,
    infoId: "",
    flowerInfo: "",
    loginId: "",
  },
  mutations: {
    //  App.vue 
    setShowLinks(state, value) {
      state.show_links = value;
    },

    // 
    setLoginStatus(state, value){
      state.loginStatus = value;
    },

    // 
    setLoginId(state, value){
      state.loginId = value;
    },

    //  (info_board)
    setFlowerInfo(state, value) {
      state.flowerInfo = value;
    },

    //  
    setInfoId(state, value) {
      state.infoId = value;
    },

  },
  actions: {

  },
  modules: {},

  plugins: [
    createPersistedState({

    })
  ]
});
