import { Options, Vue } from 'vue-class-component';
import { useStore } from 'vuex';
import Flower_Search_Dialog from '../Flower_Search_Dialog/Flower_Search_Dialog.vue';
import Top_Menu from '../Top_Menu/Top_Menu.vue';

declare namespace tmImage {
  function load(modelURL: string, metadataURL: string): Promise<any>;
  class Webcam {
    constructor(width: number, height: number, flip: boolean);
    setup(): Promise<void>;
    play(): Promise<void>;
    update(): void;
    canvas: HTMLCanvasElement;
  }
}

@Options({
  data() {
    return {
      model: null,
      webcam: null,
      labelContainer: null,
      maxPredictions: 0,
      URL: "https://teachablemachine.withgoogle.com/models/MnC6zHnxX/",
      responsive: window.innerWidth >= 800
    };
  },
  components: {
    Flower_Search_Dialog,
    Top_Menu
  },
})

export default class Flower_Search extends Vue {
  public store = useStore();
  public flower_name: string = "";
  public image: string = "";
  public picture_select: boolean = false;
  public model: any = "";
  public labelContainer: any = "";
  public maxPredictions: number = 10;
  public URL: string = "https://teachablemachine.withgoogle.com/models/MnC6zHnxX/";
  public responsive: boolean = window.innerWidth <= 800;

  public mounted() {
    //      responsive 
    window.addEventListener('resize', this.responsiveResize);
    this.init();
  }

  public beforeDestroy() {
    window.removeEventListener('resize', this.responsiveResize);
  }

  //    responsize 
  public responsiveResize() {
    this.responsive = window.innerWidth <= 800;
    if (this.responsive) {
      this.picture_select = false;
      this.image = "";
      this.flower_name = "";
      const responsiveElement = document.querySelector('.search-responsive-flower-name');
      if (responsiveElement && responsiveElement.firstChild) {
        responsiveElement.removeChild(responsiveElement.firstChild);
      }

    }
  }

  //  
  public changeFile(event: any) {
    const file = event.target.files[0]; //   
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        if (e.target) {
          const result = e.target.result as string; //  
          this.image = result;
          this.predict();
        }
      };
      reader.readAsDataURL(file);
    }
  }

  //  
  public closeDialog() {
    this.picture_select = false;
    this.image = "";
    this.flower_name = "";
  }

  // 
  public async init() {
    this.model = Object.freeze(await tmImage.load(this.URL + "model.json", this.URL + "metadata.json"));
  }

  //   
  public async predict() {
    //  
    const imageElement = document.querySelector('.flower-pic-none');
    //  
    setTimeout(async () => {
      //  
      const prediction = await this.model.predict(imageElement);
      //   
      for (let i = 0; i < this.maxPredictions; i++) {
        const classPrediction = prediction[i];
        //  60   
        if (classPrediction.probability >= 0.95) {
          const classPredictionText =
            `${classPrediction.className}: ${classPrediction.probability.toFixed(2)}`;
          console.log(classPredictionText);
          this.flower_name = classPrediction.className;
        }
      }
      this.picture_select = true;
    }, 500);
  }

  //   
  public changeResponsiveFile(event: any) {
    const responsiveElement = document.querySelector('.search-responsive-flower-name');
    if (responsiveElement) {
      responsiveElement.innerHTML = "";
    }
    this.image = "";
    const file = event.target.files[0]; //   
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        if (e.target) {
          const result = e.target.result as string; //  
          this.image = result;
          this.responsivePredict();
        }
      };
      reader.readAsDataURL(file);
    }
  }

  //    
  public async responsivePredict() {
    //     
    const responsiveElement = document.querySelector('.search-responsive-flower-name');
    const imageElement = document.querySelector('.flower-pic-none');

    //      
    if (responsiveElement && imageElement) {
      //  
      setTimeout(async () => {
        const prediction = await this.model.predict(imageElement);
        //   
        for (let i = 0; i < this.maxPredictions; i++) {
          const classPrediction = prediction[i];
          //  60   
          if (classPrediction.probability >= 0.95) {
            const classPredictionText = `${classPrediction.className}: ${classPrediction.probability.toFixed(2)}`;
            console.log(classPredictionText);
            this.flower_name = classPrediction.className;
            responsiveElement.innerHTML = "  : " + this.flower_name;
            break;
          } else {
            //      
            responsiveElement.innerHTML = "<a href='Flower_Question'>    ! </a>";
          }
        }
      }, 0);
    }
  }

}
